import React, { useState } from "react"
import { NavLink } from "react-router-dom"

type PropsType = {
    brand: string
    image: string
    price: number
    article: string
    id: number
}

let Item: React.FC<PropsType> = (props) => {
    let [onHover, setHover] = useState(false)
    let changeBehavior = () => {
        setHover(!onHover)
    }
    return <div className="col-md-4 mt-2">
        <div className="card"
            onMouseEnter={changeBehavior} onMouseLeave={changeBehavior} >
            <div className="center my-2">
                <img src={props.image}
                    className={onHover ? "ItemOn " : "ItemExit "} />
            </div>
            <div className="card-body">
                <div className="left">
                    Цена {props.price} ₽
                <br />
                <span style={{ fontSize: "0.7em" }} className="text-secondary">
                    Артикул {props.article}
                </span>
                </div>
                <div className="right">
                    {onHover ?
                        <NavLink to={`/rim/${props.id}`}>
                            <button className="btn btn-warning text-white mt-1">
                                Подробнее
                            </button>
                        </NavLink>
                        :
                        <div className="mt-3">{props.brand}</div>
                    }
                </div>
            </div>
        </div>
    </div>
}

export default Item